import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Row, Label } from "reactstrap";
import _ from "lodash";
import Loader from "../../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
import AppConfig from "../../../constants/AppConfig";
import CollapseCards from "../../../components/Cards/CollapseCards";

class LiveStockBreedCategoryForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      breedCategory: {
        Id: "",
        CategoryName: "",
        BreedId: "",
        LiveStockId: "",
        Rate: "",
        Units: "",
        Quantity: "",
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true,
        renderURL: "",
        CategoryNameRequired: false,
        BreedIdRequired: false,
        LiveStockIdRequired: false,
        RateRequired: false,
        UnitsRequired: false,
        QuantityRequired: false,
        RateInvalid: false,
        UnitsInvalid: false,
        QuantityInvalid: false
      },
      breedOptions: [],
      breedDisabled: true,
      audioGridOpen : false,
      imageGridOpen:false,
      imageAllocation :[]
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      if (this.props.activeBreedCategory.length !== 0) {
        let id = this.props.match.params.id;
        this.setCurrentBreedCategoryToState(id);
      }
      setTimeout(() => {
        this.setState({
          loading: false,
          updateFlag: true
        });
      }, 2000);
    } else {
      setTimeout(() => {
        this.setState({
          loading: false
        });
      }, 1000);
    }
    if (this.props.breedCategoryError) {
      Toaster.Toaster("Something went wrong !", this.props.breedCategoryError);
    }
  }
  setCurrentBreedCategoryToState(id) {
    let breedCategory = _.find(this.props.activeBreedCategory, function(
      category
    ) {
      return category.Id === parseInt(id);
    });
    if (breedCategory !== undefined) {
      if (breedCategory.ImageURL) {
        breedCategory.renderURL = `${AppConfig.serverURL}/${breedCategory.ImageURL}`;
      }
      let breedOptions = _.filter(this.props.breedsList, {
        LiveStockId: breedCategory.LiveStockId
      });
      let imageAllocation = _.filter(this.props.imageFiles, {
        FilePath: breedOptions.renderURL
      });
      setTimeout(() => {
        //   let activeAudioAllocation = _.filter(
        //     this.props.currentliveStockAudioAllocation,
        //     { Active: true }
        //   );
        //   let inActiveAudioAllocation = _.filter(
        //     this.props.currentliveStockAudioAllocation,
        //     { Active: false }
        //   );
        this.setState({
          updateFlag: true,
          breedCategory: breedCategory,
          loading: false,
          breedDisabled: false,
          breedOptions: breedOptions,
          // activeAudioAllocation: activeAudioAllocation,
          // inActiveAudioAllocation: inActiveAudioAllocation,
          // audioAllocation: this.props.currentliveStockAudioAllocation,
          // audioGridOpen: true,
          // videoGridOpen: true,
          // imageGridOpen: true,
           imageAllocation: imageAllocation,
          // videoAllocation: this.props.liveStockVideoAllocation
        });
      }, 3000);
    } else {
      this.props.history.push("/liveStock/liveStockBreedCategory");
    }
  }
  onChangeName(event) {
    let breedCategory = { ...this.state.breedCategory };
    breedCategory[event.target.name] = event.target.value;
    breedCategory[event.target.name + "Required"] = false;
    breedCategory[event.target.name + "Invalid"] = false;
    this.setState({ breedCategory });
  }
  onChangeStock(value) {
    let breedCategory = { ...this.state.breedCategory };
    if (value) {
      breedCategory.LiveStockId = value;
      breedCategory.LiveStockIdRequired = false;
      let breedOptions = _.filter(this.props.breedsList, {
        LiveStockId: value
      });
      this.setState({ breedCategory, breedOptions, breedDisabled: false });
    } else {
      breedCategory.LiveStockId = value;
      breedCategory.BreedId = value;
      let breedOptions = [];
      this.setState({ breedCategory, breedOptions, breedDisabled: true });
    }
  }
  onChangeBreed(value) {
    let breedCategory = { ...this.state.breedCategory };
    breedCategory.BreedId = value;
    breedCategory.BreedIdRequired = false;
    this.setState({ breedCategory });
  }
  onSubmit() {
    let breedCategory = { ...this.state.breedCategory };
    if (this.isValid()) {
      console.log("breed category", breedCategory);
      if (this.state.updateFlag) {
        let breedCategoryUpdate = _.pick(breedCategory, [
          "Id",
          "CategoryName",
          "BreedId",
          //"LiveStockId",
          "Rate",
          "Units",
          "Quantity",
          "UpdatedBy",
          "UpdatedOn",
          "Active",
        ]);
        breedCategoryUpdate.UpdatedBy = localStorage.getItem("user");
        breedCategoryUpdate.UpdatedOn = new Date();
        breedCategoryUpdate.Active = true;
        this.props.updateBreedCategory(
          breedCategoryUpdate.Id,
          breedCategoryUpdate
        );
      } else {
        let breedCategoryCreate = _.pick(breedCategory, [
          "CategoryName",
          "BreedId",
          //"LiveStockId",
          "Rate",
          "Units",
          "Quantity",
          "CreatedBy",
          "CreatedOn",
          "Active",
        ]);
        breedCategoryCreate.CreatedBy = localStorage.getItem("user");
        breedCategoryCreate.CreatedOn = new Date();
        breedCategoryCreate.Active = true;
        this.props.createBreedCategory(breedCategoryCreate);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.breedCategoryError
          ? (message = compRef.props.breedCategoryError)
          : compRef.state.updateFlag
            ? (message = "Livestock breed category updated successfully")
            : (message = "Livestock breed category created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.breedCategoryError);
        setTimeout(() => {
          if (!compRef.props.breedCategoryError) {
            compRef.onReset();
            compRef.props.history.push("/liveStock/liveStockBreedCategory");
          }
        }, 1000);
      }, 1000);
    }
  }
  isValid() {
    let breedCategory = { ...this.state.breedCategory };
    if (
      breedCategory.LiveStockId &&
      breedCategory.BreedId &&
      breedCategory.CategoryName &&
      breedCategory.CategoryName.trim().length > 0 &&
      breedCategory.Rate &&
      breedCategory.Rate >= 0 &&
      breedCategory.Units &&
      breedCategory.Units >= 0 &&
      breedCategory.Quantity &&
      breedCategory.Quantity >= 0
    ) {
      return true;
    } else {
      if (!breedCategory.LiveStockId) breedCategory.LiveStockIdRequired = true;
      if (!breedCategory.BreedId) breedCategory.BreedIdRequired = true;
      if (
        !breedCategory.CategoryName ||
        breedCategory.CategoryName.trim().length <= 0
      )
        breedCategory.CategoryNameRequired = true;
      if (breedCategory.Rate && breedCategory.Rate < 0)
        breedCategory.RateInvalid = true;
      if (!breedCategory.Rate) {
        breedCategory.RateRequired = true;
        breedCategory.RateInvalid = false;
      }
      if (breedCategory.Units && breedCategory.Units < 0)
        breedCategory.UnitsInvalid = true;
      if (!breedCategory.Units) {
        breedCategory.UnitsRequired = true;
        breedCategory.UnitsInvalid = false;
      }
      if (breedCategory.Quantity && breedCategory.Quantity < 0)
        breedCategory.QuantityInvalid = true;
      if (!breedCategory.Quantity) {
        breedCategory.QuantityRequired = true;
        breedCategory.QuantityInvalid = false;
      }
      this.setState({ breedCategory });
      return false;
    }
  }
  onReset() {
    this.setState({
      breedCategory: {
        Id: "",
        CategoryName: "",
        BreedId: "",
        LiveStockId: "",
        Rate: "",
        Units: "",
        Quantity: "",
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true,
        renderURL: "",
        CategoryNameRequired: false,
        BreedIdRequired: false,
        LiveStockIdRequired: false,
        RateRequired: false,
        UnitsRequired: false,
        QuantityRequired: false,
        RateInvalid: false,
        UnitsInvalid: false,
        QuantityInvalid: false
      }
    });
  }
  /**--------------AUDIO ALLOCATION FUNCTIONS----------------- */
  audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  onAddAudio(){
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/audioAllocation/${"liveStockBreedCategory"}/${this.props.match.params
          .id}`
      );
    }
  }
    /**------------------------Image Allocation functions ---------------------*/
    imageToggleCollapse() {
      this.setState({
        imageGridOpen: !this.state.imageGridOpen
      });
    }
    onAddImage() {
      if (this.props.match.params.id !== undefined) {
        this.props.history.push(
          `/liveStock/imageAllocation/${"liveStockBreedCategory"}/${this.props.match.params
            .id}`
        );
      }
    }
  render() {
    const breedCategory = { ...this.state.breedCategory };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Breed Category Form"
        navigation={true}
        navigationRoute="/liveStock/liveStockBreedCategory"
      >
        <div className="div-padding">
          <FormGroup row>
            {/* <Col xs="12" md={this.state.updateFlag ? "6" : "12"}> */}
            <Col xs="12" md="7">
              <FormGroup row>
                <Col xs="12" md="6">
                  <Label>Livestock name</Label>
                  <DropdownSelect
                    name="Livestock name"
                    placeholder="Select Livestock"
                    options={this.props.liveStockList}
                    value={breedCategory.LiveStockId}
                    required={breedCategory.LiveStockIdRequired}
                    onChange={event => this.onChangeStock(event)}
                  />
                </Col>
                <Col md="6">
                  <Label>Breed name</Label>
                  <DropdownSelect
                    name="Breed name"
                    placeholder="Select Breed"
                    options={this.state.breedOptions}
                    value={breedCategory.BreedId}
                    disabled={this.state.breedDisabled}
                    required={breedCategory.BreedIdRequired}
                    onChange={event => this.onChangeBreed(event)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="6">
                  <InputElement
                    type="text"
                    name="CategoryName"
                    placeholder="Category Name"
                    label="Category name"
                    maxLength={250}
                    value={breedCategory.CategoryName}
                    required={breedCategory.CategoryNameRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="number"
                    name="Rate"
                    placeholder="Rate"
                    label="Rate"
                    value={breedCategory.Rate}
                    required={breedCategory.RateRequired}
                    invalid={breedCategory.RateInvalid}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="6">
                  <InputElement
                    type="number"
                    name="Units"
                    placeholder="Units"
                    label="Units"
                    value={breedCategory.Units}
                    required={breedCategory.UnitsRequired}
                    invalid={breedCategory.UnitsInvalid}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="number"
                    name="Quantity"
                    placeholder="Quantity"
                    label="Quantity"
                    value={breedCategory.Quantity}
                    required={breedCategory.QuantityRequired}
                    invalid={breedCategory.QuantityInvalid}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
            </Col>
            {/* <Col md="5">
              {liveStock.renderURL ? (
                <img
                  src={liveStock.renderURL}
                  height={300}
                  width={350}
                  alt=""
                />
              ) : null}
            </Col> */}
          </FormGroup>
          {this.state.updateFlag ? (
            <div>
              <div style={{ marginTop: 0 }}>
                <CollapseCards
                  subName="Audio Allocation"
                  buttonName="Add Audio"
                  buttonLink={this}
                  buttonClick={this.onAddAudio.bind(this)}
                  isOpen={this.state.audioGridOpen}
                  toggleCollapse={this.audioToggleCollapse.bind(this)}
                >
                  <FormGroup row>
                    <Col xs="12">
                      {/* <AudioAllocationGrid
                        audioAllocation={this.state.activeAudioAllocation}
                        playAudio={this.playAudio.bind(this)}
                        onDelete={this.onDeleteAudio.bind(this)}
                      /> */}
                    </Col>
                  </FormGroup>
                </CollapseCards>
              </div>
              <div style={{ marginTop: -30 }}>
                <CollapseCards
                  subName="Image Allocation"
                  buttonName={
                    this.state.imageAllocation.length === 0
                      ? "Add Image"
                      : null
                  }
                  buttonLink={this}
                  buttonClick={this.onAddImage.bind(this)}
                  isOpen={this.state.imageGridOpen}
                  toggleCollapse={this.imageToggleCollapse.bind(this)}
                >
                  <FormGroup row>
                    <Col xs="12">
                      {/* <ImageAllocationGrid
                        imageAllocation={this.state.imageAllocation}
                        showImage={this.showImage.bind(this)}
                        onDelete={this.onDeleteliveStockImage.bind(this)}
                      /> */}
                    </Col>
                  </FormGroup>
                </CollapseCards>
              </div>
            </div>
          ) : null} 

          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={() => this.onReset()}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}
        </div>
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeBreedCategory: state.liveStockReducer.activeBreedCategory,
    breedsList: state.liveStockReducer.breedsList,
    liveStockList: state.liveStockReducer.liveStockList,
    breedCategoryError: state.liveStockReducer.breedCategoryError,
    imageFiles: state.mediaReducer.imageFiles,
    
  };
};

const mapDispatchToProps = dispatch => {
  return {
    createBreedCategory: breedCategory =>
      dispatch(actions.createBreedCategory(breedCategory)),
    updateBreedCategory: (id, breedCategory) =>
      dispatch(actions.updateBreedCategory(id, breedCategory))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  LiveStockBreedCategoryForm
);



