import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Row, Label } from "reactstrap";
import _ from "lodash";
import Loader from "../../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
import AppConfig from "../../../constants/AppConfig";
import CollapseCards from "../../../components/Cards/CollapseCards";

class LiveStockStepForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      liveStockStep: {
        Id: "",
        StepName: "",
        StepDescription: "",
        LiveStockId: "",
        Active: true,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        renderURL: "",
        StepNameRequired: false,
        StepDescriptionRequired: false,
        LiveStockIdRequired: false
      },
      audioGridOpen : false,
      imageGridOpen:false,
      imageAllocation :[]
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      if (this.props.activeLiveStockStep.length !== 0) {
        let id = this.props.match.params.id;
        this.setCurrentLivestockStepToState(id);
      }
      setTimeout(() => {
        this.setState({
          loading: false,
          updateFlag: true
        });
      }, 2000);
    } else {
      setTimeout(() => {
        this.setState({
          loading: false
        });
      }, 1000);
    }
    if (this.props.liveStockStepError) {
      Toaster.Toaster("Something went wrong !", this.props.liveStockStepError);
    }
  }
  setCurrentLivestockStepToState(id) {
    let liveStockStep = _.find(this.props.activeLiveStockStep, function(
      liveStockStep
    ) {
      return liveStockStep.Id === parseInt(id);
    });
    if (liveStockStep !== undefined) {
      if (liveStockStep.ImageURL) {
        liveStockStep.renderURL = `${AppConfig.serverURL}/${liveStockStep.ImageURL}`;
      }
      let imageAllocation = _.filter(this.props.imageFiles, {
        FilePath: liveStockStep.renderURL
      });
      setTimeout(() => {
        //   let activeAudioAllocation = _.filter(
        //     this.props.currentliveStockAudioAllocation,
        //     { Active: true }
        //   );
        //   let inActiveAudioAllocation = _.filter(
        //     this.props.currentliveStockAudioAllocation,
        //     { Active: false }
        //   );
        this.setState({
          updateFlag: true,
          liveStockStep: liveStockStep,
          loading: false,
          // activeAudioAllocation: activeAudioAllocation,
          // inActiveAudioAllocation: inActiveAudioAllocation,
          // audioAllocation: this.props.currentliveStockAudioAllocation,
          // audioGridOpen: true,
          // videoGridOpen: true,
          // imageGridOpen: true,
          imageAllocation: imageAllocation,
          // videoAllocation: this.props.liveStockVideoAllocation
        });
      }, 3000);
    } else {
      this.props.history.push("/liveStock/liveStockSteps");
    }
  }
  onChangeName(event) {
    let liveStockStep = { ...this.state.liveStockStep };
    liveStockStep[event.target.name] = event.target.value;
    liveStockStep[event.target.name + "Required"] = false;
    this.setState({ liveStockStep });
  }
  onChangeLiveStock(value) {
    let liveStockStep = { ...this.state.liveStockStep };
    liveStockStep.LiveStockId = value;
    liveStockStep.LiveStockIdRequired = false;
    this.setState({ liveStockStep });
  }
  onSubmit() {
    let liveStockStep = { ...this.state.liveStockStep };
    liveStockStep.StepName = liveStockStep.StepName.trim();
    liveStockStep.StepDescription = liveStockStep.StepDescription.trim();
    if (
      liveStockStep.StepName &&
      liveStockStep.StepDescription &&
      liveStockStep.LiveStockId
    ) {
      if (this.state.updateFlag) {
        liveStockStep.UpdatedBy = localStorage.getItem("user");
        liveStockStep.UpdatedOn = new Date();
        let liveStockStepUpdate = _.pick(liveStockStep, [
          "Id",
          "StepName",
          "StepDescription",
          "LiveStockId",
          "UpdatedBy",
          "UpdatedOn",
          "Active"
        ]);
        this.props.updateLiveStockStep(liveStockStepUpdate.Id, liveStockStepUpdate);
      } else {
        liveStockStep.CreatedBy = localStorage.getItem("user");
        liveStockStep.CreatedOn = new Date();
        let liveStockStepCreate = _.pick(liveStockStep, [
          "StepName",
          "StepDescription",
          "LiveStockId",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        this.props.createLiveStockStep(liveStockStepCreate);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockStepError
          ? (message = compRef.props.liveStockStepError)
          : compRef.state.updateFlag
            ? (message = "Livestock step updated successfully")
            : (message = "Livestock step created successfully");
        compRef.onReset();
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockStepError);
        setTimeout(() => {
          if (!compRef.props.liveStockStepError) {
            compRef.props.history.push("/liveStock/liveStockSteps");
          }
        }, 1000);
      }, 1000);
    } else {
      if (!liveStockStep.StepName) liveStockStep.StepNameRequired = true;
      if (!liveStockStep.StepDescription)
        liveStockStep.StepDescriptionRequired = true;
      if (!liveStockStep.LiveStockId) liveStockStep.LiveStockIdRequired = true;
      this.setState({ liveStockStep });
    }
  }
  onReset() {
    this.setState({
      liveStockStep: {
        Id: "",
        StepName: "",
        StepDescription: "",
        LiveStockId: "",
        Active: true,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        renderURL: "",
        StepNameRequired: false,
        StepDescriptionRequired: false,
        LiveStockIdRequired: false
      }
    });
  }
  /**--------------AUDIO ALLOCATION FUNCTIONS----------------- */
  audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  onAddAudio(){
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/audioAllocation/${"liveStockStep"}/${this.props.match.params
          .id}`
      );
    }
  }
    /**------------------------Image Allocation functions ---------------------*/
    imageToggleCollapse() {
      this.setState({
        imageGridOpen: !this.state.imageGridOpen
      });
    }
    onAddImage() {
      if (this.props.match.params.id !== undefined) {
        this.props.history.push(
          `/liveStock/imageAllocation/${"liveStockStep"}/${this.props.match.params
            .id}`
        );
      }
    }
  render() {
    const liveStockStep = { ...this.state.liveStockStep };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Step Form"
        navigation={true}
        navigationRoute="/liveStock/liveStockSteps"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="7">
              <FormGroup row>
                <Col xs="12" md="6">
                  <Label>Livestock name</Label>
                  <DropdownSelect
                    name="Livestock name"
                    label="Livestock name"
                    placeholder="Select Livestock name"
                    options={this.props.liveStockList}
                    value={liveStockStep.LiveStockId}
                    required={liveStockStep.LiveStockIdRequired}
                    onChange={this.onChangeLiveStock.bind(this)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="text"
                    name="StepName"
                    label="Step name"
                    maxLength={250}
                    placeholder="Step name"
                    value={liveStockStep.StepName}
                    required={liveStockStep.StepNameRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row />
              <FormGroup row>
                <Col xs="12">
                  <InputElement
                    type="textarea"
                    name="StepDescription"
                    label="Step Description"
                    maxLength={500}
                    placeholder="Step Description"
                    value={liveStockStep.StepDescription}
                    required={liveStockStep.StepDescriptionRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
            </Col>
            {/* <Col md="5">
              {liveStock.renderURL ? (
                <img
                  src={liveStock.renderURL}
                  height={300}
                  width={350}
                  alt=""
                />
              ) : null}
            </Col> */}
          </FormGroup>

         {this.state.updateFlag ? (
              <div>
                <div style={{ marginTop: 0 }}>
                  <CollapseCards
                    subName="Audio Allocation"
                    buttonName="Add Audio"
                    buttonLink={this}
                    buttonClick={this.onAddAudio.bind(this)}
                    isOpen={this.state.audioGridOpen}
                    toggleCollapse={this.audioToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        {/* <AudioAllocationGrid
                          audioAllocation={this.state.activeAudioAllocation}
                          playAudio={this.playAudio.bind(this)}
                          onDelete={this.onDeleteAudio.bind(this)}
                        /> */}
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
                <div style={{ marginTop: -30 }}>
                  <CollapseCards
                    subName="Image Allocation"
                    buttonName={
                      this.state.imageAllocation.length === 0
                        ? "Add Image"
                        : null
                    }
                    buttonLink={this}
                    buttonClick={this.onAddImage.bind(this)}
                    isOpen={this.state.imageGridOpen}
                    toggleCollapse={this.imageToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        {/* <ImageAllocationGrid
                          imageAllocation={this.state.imageAllocation}
                          showImage={this.showImage.bind(this)}
                          onDelete={this.onDeleteliveStockImage.bind(this)}
                        /> */}
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
              </div>
            ) : null} 

          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={() => this.onReset()}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}
        </div>
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockStep: state.liveStockReducer.activeLiveStockStep,
    liveStockList: state.liveStockReducer.liveStockList,
    liveStockStepError: state.liveStockReducer.liveStockStepError,
    imageFiles: state.mediaReducer.imageFiles,
    
  };
};

const mapDispatchToProps = dispatch => {
  return {
    createLiveStockStep: liveStockStep => dispatch(actions.createLiveStockStep(liveStockStep)),
    updateLiveStockStep: (id, liveStockStep) =>
      dispatch(actions.updateLiveStockStep(id, liveStockStep))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(LiveStockStepForm);



